.App {
    font-family: Arial, sans-serif;
    text-align: center;
}

.progress {
    position: fixed;
    top: 10px;
    left: 10px;
    display: flex;
    gap: 15px;
    font-size: 18px;
}

.type {
    cursor: pointer;
    opacity: 0.6;
}

.type.active {
    font-weight: bold;
    opacity: 1;
}

.character-display {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
    user-select: none;
}

.japanese-character {
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.pronunciation {
    font-size: 50px;
    margin-top: 20px;
    transition: opacity 0.3s ease;
}

.complete-message {
    position: fixed;
    bottom: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
    font-size: 24px;
}
